import { gql } from '@apollo/client/core'

const homeQuery = gql`
  query Home {
    Courses {
      title
      image
      id
      Instructor {
        name
      }
      Category {
        name
      }
    }
    Categories(order_by: { id: asc }) {
      image
      name
    }
    Instructors {
      name
      icon
      id
    }
  }
`

export default homeQuery
