<template>
  <div class="wrap">
    <div class="search-wrap">
      <h1 class="headline">{{ $t('search.title') }}</h1>
      <form class="search-form">
        <label>
          <input type="text" class="search-input" :placeholder="$t('search.input')" v-model="state.searchText" />
          <font-awesome-icon :icon="searchIcon" class="icon" />
        </label>
        <button type="submit" class=""></button>
      </form>
    </div>
    <!--    Ë¨õÂ∏´Ê§úÁ¥¢-->
    <div v-if="searchTeachers?.length" class="search-result">
      <h2 class="headline">{{ $t('search.teacher') }}</h2>
      <div class="item-wrap">
        <TeacherItem
          v-for="(instructor, index) in searchTeachers"
          :key="index"
          :image="instructor.icon"
          :teacherName="instructor.name"
          :id="instructor.id"
          class="item"
        />
      </div>
    </div>
    <!--    „Ç≥„Éº„ÇπÊ§úÁ¥¢-->
    <div v-if="searchCourses?.length" class="search-result">
      <h2 class="headline">{{ $t('search.course') }}</h2>
      <CourseItems :courses="searchCourses" />
    </div>
    <p v-if="state.searchText.length && !searchCourses?.length && !searchTeachers?.length" class="no-result">
      {{ state.searchText }}„Åß„ÅÆÊ§úÁ¥¢„ÅØË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì„Åß„Åó„Åüüò≠
    </p>
    <!--    „Ç´„ÉÜ„Ç¥„É™„Éº„É™„Çπ„Éà-->
    <div class="category-wrap">
      <h2 class="headline">{{ $t('search.category') }}</h2>
      <div class="categories">
        <CategoryItem
          v-for="(category, index) in categories"
          :key="index"
          :category-name="category.name"
          :image="category.image"
          class="category-item"
        />
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, reactive } from 'vue'
import { useQuery, useResult } from '@vue/apollo-composable'
import CategoryItem from '@/components/CategoryItem.vue'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import homeQuery from '@/graphql/queries/home.query'
import { HomeQuery } from '@/graphql/@types/types'
import { useStore } from 'vuex'
import CourseItems from '@/components/CourseItems.vue'
import TeacherItem from '@/components/TeacherItem.vue'

export default defineComponent({
  components: {
    CategoryItem,
    CourseItems,
    TeacherItem
  },
  setup() {
    const store = useStore()
    const state = reactive({
      searchText: ''
    })

    const { result } = useQuery<HomeQuery>(homeQuery)
    const courses = useResult(result, null, (data) => data.Courses)
    const categories = useResult(result, null, (data) => data.Categories)
    const instructors = useResult(result, null, (data) => data.Instructors)

    const searchCourses = computed(() => {
      if (state.searchText === '') return

      return courses?.value?.filter((course) => {
        return (
          course.title.includes(state.searchText) ||
          course.Instructor.name.includes(state.searchText) ||
          course.Category.name.includes(state.searchText)
        )
      })
    })

    const searchTeachers = computed(() => {
      if (state.searchText === '') return

      return instructors?.value?.filter((teacher) => {
        return teacher.name.includes(state.searchText)
      })
    })

    const setLoading = (value: boolean) => {
      store.commit('setLoading', value)
    }
    setLoading(false)

    const searchIcon = computed(() => faSearch)

    return {
      state,
      searchIcon,
      setLoading,
      searchCourses,
      searchTeachers,
      categories,
      instructors
    }
  }
})
</script>

