
import { computed, defineComponent, reactive } from 'vue'
import { useQuery, useResult } from '@vue/apollo-composable'
import CategoryItem from '@/components/CategoryItem.vue'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import homeQuery from '@/graphql/queries/home.query'
import { HomeQuery } from '@/graphql/@types/types'
import { useStore } from 'vuex'
import CourseItems from '@/components/CourseItems.vue'
import TeacherItem from '@/components/TeacherItem.vue'

export default defineComponent({
  components: {
    CategoryItem,
    CourseItems,
    TeacherItem
  },
  setup() {
    const store = useStore()
    const state = reactive({
      searchText: ''
    })

    const { result } = useQuery<HomeQuery>(homeQuery)
    const courses = useResult(result, null, (data) => data.Courses)
    const categories = useResult(result, null, (data) => data.Categories)
    const instructors = useResult(result, null, (data) => data.Instructors)

    const searchCourses = computed(() => {
      if (state.searchText === '') return

      return courses?.value?.filter((course) => {
        return (
          course.title.includes(state.searchText) ||
          course.Instructor.name.includes(state.searchText) ||
          course.Category.name.includes(state.searchText)
        )
      })
    })

    const searchTeachers = computed(() => {
      if (state.searchText === '') return

      return instructors?.value?.filter((teacher) => {
        return teacher.name.includes(state.searchText)
      })
    })

    const setLoading = (value: boolean) => {
      store.commit('setLoading', value)
    }
    setLoading(false)

    const searchIcon = computed(() => faSearch)

    return {
      state,
      searchIcon,
      setLoading,
      searchCourses,
      searchTeachers,
      categories,
      instructors
    }
  }
})
